import React from 'react';

import DateTypeForm from './DateTypeForm';
import ShowJsonText from '../../common/ShowJsonText';
// import SchemaViewer from '../../common/SchemaViewer.js';

// const resolvedSchema = require('json-schema-loader!../../../schema/common/json/base/DateType.json');


class DateTypePage extends React.Component {
  constructor(props) {
      super(props);
      console.log(props);
      this.state = {
        ...props,
        text: JSON.stringify(props.value, null, 2)
      }
  }
  render() {
    return (
      <div className="row">
        <div className="col-md-6 col-md-offset-3">
          <DateTypeForm {...this.state} />
        </div>
        <div className="col-md-3 well">
          <ShowJsonText {...this.state} />
        </div>
        {/* <SchemaViewer value={resolvedSchema} /> */}
      </div>
    );
  }
}
DateTypePage.propTypes = {
  // userSignupRequest: React.PropTypes.func.isRequired,
  // addFlashMessage: React.PropTypes.func.isRequired
};

export default DateTypePage;



